/**
 *  
 * @example
 * pluralize(12.21, ['', '', '']);
 * pluralize(7, ['', '', '']);
 * pluralize(-121, ['', '', '']);
 */
export const pluralize = (
  value: number,
  words: [string, string, string],
): string => {
  value = Math.abs(value) % 100;
  const num = value % 10;

  if (value > 10 && value < 20) return words[2];

  if (num > 1 && num < 5) return words[1];

  if (num === 1) return words[0];

  return words[2];
};
